___CSS_LOADER_EXPORT___.push([module.id, `/* Icon 컴포넌트 스타일 */
.icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  color: inherit; /* 부모 요소에서 색상 상속 */
}

/* 아이콘 크기 클래스 */
.icon svg {
  width: 100%;
  height: 100%;
}

/* 시멘틱 토큰을 활용한 아이콘 크기 */
.icon-xs {
  width: var(--semantic-size-icon-xs, 16px);
  height: var(--semantic-size-icon-xs, 16px);
}

.icon-sm {
  width: var(--semantic-size-icon-sm, 20px);
  height: var(--semantic-size-icon-sm, 20px);
}

.icon-md {
  width: var(--semantic-size-icon-md, 24px);
  height: var(--semantic-size-icon-md, 24px);
}

.icon-lg {
  width: var(--semantic-size-icon-lg, 32px);
  height: var(--semantic-size-icon-lg, 32px);
}

.icon-xl {
  width: var(--semantic-size-icon-xl, 40px);
  height: var(--semantic-size-icon-xl, 40px);
} `, "",{"version":3,"sources":["webpack://./shared/components/Icon/Icon.css"],"names":[],"mappings":"AAAA,kBAAkB;AAClB;EACE,oBAAoB;EACpB,mBAAmB;EACnB,uBAAuB;EACvB,cAAc,EAAE,kBAAkB;AACpC;;AAEA,eAAe;AACf;EACE,WAAW;EACX,YAAY;AACd;;AAEA,uBAAuB;AACvB;EACE,yCAAyC;EACzC,0CAA0C;AAC5C;;AAEA;EACE,yCAAyC;EACzC,0CAA0C;AAC5C;;AAEA;EACE,yCAAyC;EACzC,0CAA0C;AAC5C;;AAEA;EACE,yCAAyC;EACzC,0CAA0C;AAC5C;;AAEA;EACE,yCAAyC;EACzC,0CAA0C;AAC5C","sourcesContent":["/* Icon 컴포넌트 스타일 */\r\n.icon {\r\n  display: inline-flex;\r\n  align-items: center;\r\n  justify-content: center;\r\n  color: inherit; /* 부모 요소에서 색상 상속 */\r\n}\r\n\r\n/* 아이콘 크기 클래스 */\r\n.icon svg {\r\n  width: 100%;\r\n  height: 100%;\r\n}\r\n\r\n/* 시멘틱 토큰을 활용한 아이콘 크기 */\r\n.icon-xs {\r\n  width: var(--semantic-size-icon-xs, 16px);\r\n  height: var(--semantic-size-icon-xs, 16px);\r\n}\r\n\r\n.icon-sm {\r\n  width: var(--semantic-size-icon-sm, 20px);\r\n  height: var(--semantic-size-icon-sm, 20px);\r\n}\r\n\r\n.icon-md {\r\n  width: var(--semantic-size-icon-md, 24px);\r\n  height: var(--semantic-size-icon-md, 24px);\r\n}\r\n\r\n.icon-lg {\r\n  width: var(--semantic-size-icon-lg, 32px);\r\n  height: var(--semantic-size-icon-lg, 32px);\r\n}\r\n\r\n.icon-xl {\r\n  width: var(--semantic-size-icon-xl, 40px);\r\n  height: var(--semantic-size-icon-xl, 40px);\r\n} "],"sourceRoot":""}]);
